form {
    display: flex;
    margin: 0 auto 30px;
    justify-content: center;
  }
  
  button {
    background: #0099ff;
    /* border: none; */
    outline: 1px solid #09f;
    color: #fff;
    padding: 8px 16px;
    font-size: 16px;
    border-radius: 10px 0 0 10px;
  }

  button:hover {
    background: rgb(41, 169, 255);
  }
  
  input[type=text] {
    border-radius: 0 10px 10px 0;
    font-size: 16px;
    /* border: none; */
    outline: 1px solid #09f;
    padding: 4px 16px;
  }

  input[type=text]:focus {
    /* border: none; */
    outline: 1px solid #09f;
  }